html {
  max-width: 100%;
  min-height: 100vh;
  background-image: linear-gradient(-225deg, #e2ffd2 0%, #fdf2cd 100%);
  font-family: "Cabin", sans-serif;
}

h1 {
  font-size: 84px;
  text-align: center;
}

h1 span {
  display: inline-block;
  animation: float 0.2s ease-in-out infinite;
}

@keyframes float {
  0%,
  100% {
    transform: none;
  }
  33% {
    transform: translateY(-1px) rotate(-2deg);
  }
  66% {
    transform: translateY(1px) rotate(2deg);
  }
}
body:hover span {
  animation: bounce 0.6s;
}
@keyframes bounce {
  0%,
  100% {
    transform: translate(0);
  }
  25% {
    transform: rotateX(20deg) translateY(2px) rotate(-3deg);
  }
  50% {
    transform: translateY(-20px) rotate(3deg) scale(1.1);
  }
}

span:nth-child(4n) {
  color: hsl(50, 75%, 55%);
  text-shadow: 1px 1px hsl(50, 75%, 45%), 2px 2px hsl(50, 45%, 45%),
    3px 3px hsl(50, 45%, 45%), 4px 4px hsl(50, 75%, 45%);
}
span:nth-child(4n-1) {
  color: hsl(135, 35%, 55%);
  text-shadow: 1px 1px hsl(135, 35%, 45%), 2px 2px hsl(135, 35%, 45%),
    3px 3px hsl(135, 35%, 45%), 4px 4px hsl(135, 35%, 45%);
}
span:nth-child(4n-2) {
  color: hsl(155, 35%, 60%);
  text-shadow: 1px 1px hsl(155, 25%, 50%), 2px 2px hsl(155, 25%, 50%),
    3px 3px hsl(155, 25%, 50%), 4px 4px hsl(140, 25%, 50%);
}
span:nth-child(4n-3) {
  color: hsl(30, 65%, 60%);
  text-shadow: 1px 1px hsl(30, 45%, 50%), 2px 2px hsl(30, 45%, 50%),
    3px 3px hsl(30, 45%, 50%), 4px 4px hsl(30, 45%, 50%);
}

h1 span:nth-child(2) {
  animation-delay: 0.05s;
}
h1 span:nth-child(3) {
  animation-delay: 0.1s;
}
h1 span:nth-child(4) {
  animation-delay: 0.15s;
}
h1 span:nth-child(5) {
  animation-delay: 0.2s;
}
h1 span:nth-child(6) {
  animation-delay: 0.25s;
}
h1 span:nth-child(7) {
  animation-delay: 0.3s;
}
h1 span:nth-child(8) {
  animation-delay: 0.35s;
}
h1 span:nth-child(9) {
  animation-delay: 0.4s;
}
h1 span:nth-child(10) {
  animation-delay: 0.45s;
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
  border-radius: 10px 0px 0px 10px;
  outline: none;
}

.search-button {
  background-color: gray;
  border: none;
  padding: 10px 20px;
  color: white;
  border-radius: 0px 10px 10px 0px;
  cursor: pointer;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.card {
  width: 50%;
  background-color: #ffffff;
  display: grid;
  place-items: center;
  opacity: 0.9;
  margin: 20px;
  border-radius: 20px;
}

.ingredients-list {
  list-style-type: none;
  text-align: center;
  width: 80%;
}

.recipe-img {
  border-radius: 40px;
}

.recipe-inf p {
  text-align: center;
  margin: 10px;
}

ul {
  padding: 0;
}

@media (max-width: 600px) {
  .card {
    width: 100%;
  }

  h2 {
    text-align: center;
  }

  .recipe-img {
    width: 80%;
  }

  .ingredients-list {
    width: 80%;
  }
}
